.card-black > :not([class*="card-media"]) .tm-h5,
.card-black > :not([class*="card-media"]) .tm-h6,
.overlay-blue .tm-h4,
.overlay-blue .tm-h5,
.overlay-blue .tm-h6,
.tm-navbar-container:not(.navbar-transparent) .tm-h4,
.tm-navbar-container:not(.navbar-transparent) .tm-h5,
.tm-navbar-container:not(.navbar-transparent) .tm-h6 {
