@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");
body {
  overflow: hidden;
}
.tm-sidebar-left {
  position: fixed;
  top: 80px;
  bottom: 0;
  box-sizing: border-box;
  width: 240px !important;
  padding: 40px 40px 60px 40px;
  border-right: 1px #e5e5e5 solid;
  overflow: auto;
}
.tm-sidebar-right {
  position: absolute;
  top: 0;
  left: calc(100% + 0px);
  width: 200px;
}
@media (min-width: 960px) {
  .tm-sidebar-left + .tm-main {
    padding-left: 240px;
  }
}
@media (min-width: 1200px) {
  .tm-sidebar-right {
    left: calc(100% + 0px);
  }
  .tm-sidebar-left + .tm-main {
    padding-left: 40px;
  }
}
@media (min-width: 1400px) {
  .tm-sidebar-left {
    width: 300px !important;
    padding: 45px 45px 60px 45px;
  }
  .tm-sidebar-right {
    left: calc(100% + 60px);
  }
  .tm-sidebar-left + .tm-main {
    padding-left: 40px;
  }
}
.tm-navbar-container:not(.navbar-transparent) {
  background: linear-gradient(to left, #28a5f5, #1e87f0);
}
.tm-navbar-container .navbar-nav > li > a,
.tm-navbar-container .navbar-item:not(.logo),
.tm-navbar-container .navbar-toggle {
  height: 70px;
  font-size: 13px;
}
.tm-navbar-container .navbar-nav > li > a {
  position: relative;
}
.tm-navbar-container .navbar-nav > li > a::before {
  content: "";
  display: block;
  position: absolute;
  left: 15px;
  right: calc(102% - 15px);
  bottom: 20px;
  height: 1px;
  background-color: currentColor;
  transition: 0.3s ease-in-out;
  transition-property: right;
}
.tm-navbar-container .navbar-nav > li > a:hover::before {
  right: 15px;
}
.tm-nav > li.active > a {
  position: relative;
}
.tm-nav > li.active > a:before {
  content: "";
  position: absolute;
  top: 15px;
  left: -25px;
  width: 15px;
  border-top: 1px solid #1e87f0;
}
.tm-nav .nav-header {
  padding: 8px 0;
  border-bottom: 1px solid #e5e5e5;
}
@media (min-width: 960px) {
  .tm-subnav {
    margin-left: -50px;
  }
  .tm-subnav > * {
    padding-left: 50px;
  }
}
.tm-subnav > * > :first-child {
  text-transform: capitalize;
}
.tm-label-changelog {
  width: 90px;
